@keyframes fadeInOut {
    0%{
        transform: translateY(2rem);
        opacity: 0;
    }

    50%{
        transform: translateY(0);
        opacity: 1;
    }

    100%{
        transform: translateY(-2rem);
        opacity: 0;
    }
}

