.active{
    color: #5C63FF;;
    &::after {
        transform: scale(1);
    }
}

h2{
    transition: .1s all;
    cursor: pointer;
    position: relative;


    &::after {
        content: "";
        position: absolute;
        background: white;
        height: 2px;
        width: 40%;
        bottom: -5px;
        left: 0;
        transform-origin: left;
        transition: 0.3s all;
        transform: scale(0);
      }
    
      &:hover {
        color: #5C63FF;
    
        &::after {
          transform: scale(1);
        }
      }


    &:first-child{
        border-right: 1px solid #fefefe88;
        padding-right: 1.5rem;
    }

    &:last-child{
        margin-left: 1.5rem;
    }
    
}


